.typing-indicator {
  will-change: transform;
  background-color: #e6e7ed;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  width: fit-content;
  min-width: 50px;
  min-height: 36px;
  margin: 1vh 0 0 10px;
  padding: 0;
  animation: 2s ease-out infinite bulge;
  display: none;
  position: relative;
}

.typing-indicator:before {
  content: "";
  background-color: #e6e7ed;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  position: absolute;
  bottom: -1px;
  left: 0;
}

.typing-indicator:after {
  content: "";
  background-color: #e6e7ed;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  position: absolute;
  bottom: -5px;
  left: -6px;
}

.typing-indicator span {
  opacity: .4;
  background-color: #9e9ea1;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  margin: 0 1px;
  display: block;
}

.typing-indicator span:first-of-type {
  animation: 1s .3333s infinite blink;
}

.typing-indicator span:nth-of-type(2) {
  animation: 1s .6666s infinite blink;
}

.typing-indicator span:nth-of-type(3) {
  animation: 1s .9999s infinite blink;
}

@keyframes blink {
  50% {
    opacity: 1;
  }
}

@keyframes bulge {
  50% {
    transform: scale(1.05);
  }
}

